import React, { useEffect } from 'react';
import { Button, ListGroup, Row, Col, Image, Card } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { orderCreateAction } from '../actions/orderAction';
import { Link } from 'react-router-dom';
import Message from '../components/shared/Message';
import CheckOutStep from '../components/shared/CheckOutSteps'

const PlaceOrder = ({ history }) => {
    const cart = useSelector(state => state.cart);
    // const { cartItems, shippingAddress } = cart;

    const dispatch = useDispatch()

    const orderCreate = useSelector(state => state.orderCreate);
    const { order, loading, success, error } = orderCreate;

    const addDecimal = (num) => {
        return Math.round(num * 100 / 100).toFixed(2);
    }

    cart.itemsPrice = addDecimal(cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0));
    cart.shippingPrice = addDecimal(cart.cartItems > 500 ? 0 : 50);
    cart.taxPrice = addDecimal(Number(0.15 * cart.itemsPrice).toFixed(2))
    cart.totalPrice = Number(cart.itemsPrice) + Number(cart.shippingPrice) + Number(cart.taxPrice)
    console.log(cart.totalPrice);
    const placeOrder = () => {
        dispatch(orderCreateAction({
            orderItems: cart.cartItems,
            shippingAddress: cart.shippingAddress,
            paymentMethod: cart.paymentMethod,
            itemsPrice: cart.itemsPrice,
            shippingPrice: cart.shippingPrice,
            taxPrice: cart.taxPrice,
            totalPrice: cart.totalPrice,


        }))

    }

    useEffect(() => {
        if (success) {
            history.push(`/order/${order._id}`);
            window.location.reload(false);
        }
        //eslint-disable-next-line

    }, [history, success])

    return (
        <>
            <CheckOutStep step1 step2 step3 step4 />
            <Row>
                <Col md={8}>
                    <ListGroup variant="flush">
                        <ListGroup.Item>
                            <h2>Shipping</h2>

                            <p>
                                <strong>Address: </strong>
                                {cart.shippingAddress.address}
                                {cart.shippingAddress.city}
                                {cart.shippingAddress.postalCode}
                                {cart.shippingAddress.country}
                            </p>

                        </ListGroup.Item>
                        <ListGroup.Item>
                            <h2>Payment Method</h2>
                            <p>
                                <strong> {cart.paymentMethod} </strong>
                            </p>

                        </ListGroup.Item>
                        <ListGroup.Item>
                            <h2>Order Items</h2>
                            {
                                cart.cartItems === 0 ? (<Message variant="danger">Your cart is empty</Message>) : (
                                    <ListGroup variant="flush">
                                        {
                                            cart.cartItems.map((item, index) => (
                                                <ListGroup.Item key={index}>
                                                    <Row>
                                                        <Col md={1}>
                                                            <Image src={item.image} alt={item.name} fluid />
                                                        </Col>
                                                        <Col>
                                                            <Link to={`/product/${item.product_id}`}>{item.name}</Link>
                                                        </Col>
                                                        <Col md={4}>
                                                            {item.qty + " x " + " $" + item.price} =  ${(item.qty * item.price).toFixed(2)}
                                                        </Col>
                                                    </Row>
                                                </ListGroup.Item>
                                            ))
                                        }
                                    </ListGroup>
                                )
                            }
                        </ListGroup.Item>
                    </ListGroup>
                </Col>
                <Col md={4}>
                    <Card>
                        <ListGroup variant="flush">
                            <ListGroup.Item>
                                <h2>Order Summary</h2>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Items</Col>
                                    <Col>{cart.itemsPrice}</Col>
                                </Row>
                                <Row>
                                    <Col>Shipping</Col>
                                    <Col>{cart.shippingPrice}</Col>
                                </Row>
                                <Row>
                                    <Col>Tax</Col>
                                    <Col>{cart.taxPrice}</Col>
                                </Row>
                                <Row>
                                    <Col>Total</Col>
                                    <Col>{cart.totalPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                {error && <Message variant="danger">{error}</Message>}
                            </ListGroup.Item>
                            <Button type="button"
                                className="btn-block"
                                disabled={cart.cartItems === 0}
                                onClick={placeOrder}
                            >Continue</Button>
                        </ListGroup>
                    </Card>
                </Col>

            </Row>
        </>
    )

}

export default PlaceOrder;