import { OREDR_CREATE_REQUEST, OREDR_CREATE_SUCCESS, OREDR_CREATE_FAIL, OREDR_DETAILS_SUCCESS, OREDR_DETAILS_FAIL, OREDR_DETAILS_REQUEST, OREDR_PAY_REQUEST, OREDR_PAY_SUCCESS, OREDR_PAY_FAIL, OREDR_PAY_RESET, OREDR_LIST_MY_REQUEST, OREDR_LIST_MY_SUCCESS, OREDR_LIST_MY_FAIL, ORDER_LIST_MY_RESET } from '../constant/orderConstant';

export const orderCreateReducer = (state = {}, action) => {
    switch (action.type) {
        case OREDR_CREATE_REQUEST:
            return { loading: true }
        case OREDR_CREATE_SUCCESS:
            return {
                loading: false,
                success: true,
                order: action.payload
            }
        case OREDR_CREATE_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default: return state;
    }

}

export const OrderDetailsReducer = (state = { orderItems: [], loading: true, shippingAddress: {} }, action) => {
    switch (action.type) {
        case OREDR_DETAILS_REQUEST:
            return {
                ...state,
                loading: true
            }
        case OREDR_DETAILS_SUCCESS:
            return {
                loading: false,
                order: action.payload
            }
        case OREDR_DETAILS_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default: return state;
    }
}

export const orderPayReducer = (state = {}, action) => {
    switch (action.type) {
        case OREDR_PAY_REQUEST:
            return {

                loading: true
            }
        case OREDR_PAY_SUCCESS:
            return {
                loading: false,
                success: true
            }
        case OREDR_PAY_FAIL:
            return {
                loading: false,
                error: action.payload
            }

        case OREDR_PAY_RESET:
            return {}

        default: return state;
    }
}

export const orderListReducer = (state = { orders: [] }, action) => {
    switch (action.type) {
        case OREDR_LIST_MY_REQUEST:
            return {

                loading: true
            }
        case OREDR_LIST_MY_SUCCESS:
            return {
                loading: false,
                orders: action.payload
            }
        case OREDR_LIST_MY_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        case ORDER_LIST_MY_RESET:
            return { orders: [] };


        default: return state;
    }
}