import {
    PRODUCT_LIST_REQUEST, PRODUCT_LIST_SUCCESS, PRODUCT_LIST_FAILS, ADD_PRODUCT_FAILS,
    PRODUCT_DETAILS_REQUEST, PRODUCT_DETAILS_SUCCESS, PRODUCT_DETAILS_FAILS,
    ADD_PRODUCT_REQUEST, ADD_PRODUCT_SUCCESS,
} from '../constant/productConstant'

export const productListReducer = (state = { products: [] }, action) => {
    switch (action.type) {
        case PRODUCT_LIST_REQUEST:
            return { loading: true, products: [] }

        case PRODUCT_LIST_SUCCESS:
            return { loading: false, products: action.payload }

        case PRODUCT_LIST_FAILS:
            return { loading: false, error: action.payload }

        default:
            return state;

    }
}

export const productDetailsReducer = (state = { product: { reviews: [] } }, action) => {
    switch (action.type) {
        case PRODUCT_DETAILS_REQUEST:
            return { loading: true, ...state }
        case PRODUCT_DETAILS_SUCCESS:
            return { loading: false, product: action.payload }
        case PRODUCT_DETAILS_FAILS:
            return { loading: false, error: action.payload }
        default:
            return state;
    }
}

export const addProduct = (state = {}, action) => {
    switch (action.type) {
        case ADD_PRODUCT_REQUEST:
            return {
                loading: true
            }
        case ADD_PRODUCT_SUCCESS:
            return {
                loading: false,
                products: action.payload
            }
        case ADD_PRODUCT_FAILS:
            return {
                loading: false,
                error: action.payload
            }

        default:
            return state;

    }
}