import { CART_ADD_ITEM, CART_REMOVE_ITEM, CART_SAVE_SHIPPING_ADDRESS, CART_SAVE_PAYMENT_METHOD } from "../constant/cartConstant";

export const cartReducer = (state = { cartItems: [] }, action) => {
    switch (action.type) {
        case CART_ADD_ITEM:
            //storing the product in item from action payload
            const item = action.payload;
            //to check item is present or empty using find() method 
            // here product is id of product
            const existItem = state.cartItems.find(x => x.product_id === item.product_id)
            if (existItem) {
                return {
                    ...state,
                    cartItems: state.cartItems.map(x => x.product_id === existItem.product_id ? item : x)
                }

            } else {
                return {
                    ...state,
                    cartItems: [...state.cartItems, item]
                }
            }

        case CART_REMOVE_ITEM:
            return {
                ...state,
                //here x.product is id of product as we declared id as product in action file
                cartItems: state.cartItems.filter((x) => x.product_id !== action.payload)
            }

        case CART_SAVE_SHIPPING_ADDRESS:
            return {
                ...state,
                shippingAddress: action.payload
            }

        case CART_SAVE_PAYMENT_METHOD:
            return {
                ...state,
                paymentMethod: action.payload
            }
        default:
            return state;
    }
}