import { createStore, combineReducers, applyMiddleware } from 'redux';
// it is a middle ware
import thunk from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension';
// importing product reducer
import { productListReducer, productDetailsReducer, addProduct } from './reducers/productReducer';
//importing cartReducer
import { cartReducer } from './reducers/cartReducer';
import { orderCreateReducer, OrderDetailsReducer, orderPayReducer, orderListReducer } from './reducers/orderReducer'
import { userLoginReducer, userRegisterReducer, userDetailReducer, userProfileUpdateReducer } from "./reducers/userReducers";


const shippingFromLocalStorage = localStorage.getItem('shippingAddress') ? JSON.parse(localStorage.getItem('shippingAddress')) : {}


//const userInfoFromStorage = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : null;
const userInfoFromStorage = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : null;

// to store data that we will get from local storage
const cartItemsFromStorage = localStorage.getItem('cartItems') ? JSON.parse(localStorage.getItem('cartItems')) : [];

// in reducer we create the function for CRUD opertion to maintain the project flow
const reducer = combineReducers({
    productList: productListReducer,
    productDetails: productDetailsReducer,
    cart: cartReducer,
    userLogin: userLoginReducer,
    userRegister: userRegisterReducer,
    userDetails: userDetailReducer,
    userUpdateProfile: userProfileUpdateReducer,
    orderCreate: orderCreateReducer,
    orderDetails: OrderDetailsReducer,
    orderPay: orderPayReducer,
    orderListMy: orderListReducer,
    yourProduct: addProduct
});
// we defined the default state value and what changes occours
//this is our global state
const initialState = {
    //cart: { cartItems: 'unigrasp' }
    cart: { cartItems: cartItemsFromStorage, shippingAddress: shippingFromLocalStorage },
    userLogin: { userInfo: userInfoFromStorage }
};
const middleware = [thunk];

const store = createStore(
    reducer,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
